.emoji-picker-box > .box{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-bottom: 20px;
}
.emoji-picker-box > .box > div:first-child{
    flex: 1;
}
.emoji-picker-box > .box > *:nth-child(2){
    width: 42px;
    padding: 8px 10px;
}
.emoji-picker-box > .hidden{
    display: none;
}